import { Element, Island, Monster } from "./element";

import { getIconPath, getMonstersByElements } from './utils';

export function defaultPlantIsland(): Island {
    const elements: Element[] = [
        Element.Plant,
        Element.Cold,
        Element.Water,
        Element.Earth
    ];

    const monsters = getMonstersByElements(elements);

    return {
        name: "Plant",
        ...defaultIslandState(monsters[0]),
        availableElements: elements,
        availableMonsters: monsters,        
        availableElementsIcons: elements.map(element => getIconPath(element)),
        selectedMonster: monsters[0],
        index: 0
    }
}


