export function stationFactory(index: number, selectedMonster: Monster) {
    return {
        upgraded: false,
        selectedMonster: selectedMonster,
        index: index,
        endTimerString: "",
        endTimer: null
    } 
}

function defaultIslandState(selectedMonster: Monster) {
    return {
        breedingStations: [
            stationFactory(0, selectedMonster)
        ],
        nurseryStations: [
            stationFactory(0, selectedMonster),
        ],
    }
}