// function makeHumanReadable(num: any, singular:any) {
//     return num > 0
//       ? num + (num === 1 ? ` ${singular}, ` : ` ${singular}s, `)
//       : '';
// }

function makeHumanReadable(num: any, singular:any) {
    return num > 0
      ? num + `${singular} `
      : '';
}

function toDaysMinutesSeconds(totalSeconds: number) {
    const seconds = Math.floor(totalSeconds % 60);
    const minutes = Math.floor((totalSeconds % 3600) / 60);
    const hours = Math.floor((totalSeconds % (3600 * 24)) / 3600);
    const days = Math.floor(totalSeconds / (3600 * 24));
  
    const secondsStr = makeHumanReadable(seconds, 's');
    const minutesStr = makeHumanReadable(minutes, 'm');
    const hoursStr = makeHumanReadable(hours, 'h');
    const daysStr = makeHumanReadable(days, 'd');
  
    return `${daysStr}${hoursStr}${minutesStr}${secondsStr}`.replace(/,\s*$/, '');
}

export const allMonstersWithIcons : Monster[] = allMonsters.map(monster => {
    monster.elementIcons = monster.elements.map(element => getIconPath(element));
    monster.monsterIcon = getMonsterIconPath(monster.name)
    monster.breedingTimeString = toDaysMinutesSeconds(monster.breedingTimeInSeconds);
    monster.breedingTimeEnhancedString = toDaysMinutesSeconds(monster.breedingTimeInSecondsEnhanced);
    return monster;
})