
import {
  defineComponent,
  PropType,
  ref,
  toRef,
  toRefs,
  defineProps,
  computed,
  onMounted,
} from "vue";
import { allMonsters } from "./all-monsters";
import { Station, Island, Monster } from "./element";
import { useMainStore } from "./main-store";

interface TimeObject {
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
}

function getTimeUntil(endDate: Date): TimeObject {
  let endDateTime = endDate.getTime();
  let now = new Date().getTime();
  let timeleftDate = endDateTime - now;

  let days = Math.floor(timeleftDate / (1000 * 60 * 60 * 24));
  let hours = Math.floor((timeleftDate % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
  let minutes = Math.floor((timeleftDate % (1000 * 60 * 60)) / (1000 * 60));
  let seconds = Math.floor((timeleftDate % (1000 * 60)) / 1000);

  return {
    days,
    hours,
    minutes,
    seconds,
  } as TimeObject;
}

function timeUntilToString(timeUntil: TimeObject): string {
  const { days, hours, minutes, seconds } = timeUntil;
  let output = "";

  if (days > 0) output += `${days}d`;

  if (hours > 0 || days > 0) output += `${hours}h`;

  if (minutes > 0 || days > 0 || hours > 0) output += `${minutes}m`;

  if (seconds > 0 || days > 0 || hours > 0 || minutes > 0) output += `${seconds}s`;

  return output;
}

function ensureDateIsDateObject(station: Station): void {
  if (!station || !station.endTimer) return;

  if (!(station.endTimer instanceof Date)) {
    station.endTimer = new Date(station.endTimer);
  }
}

function setEndTimer(station: Station): void {
  if (!station.selectedMonster) return;

  let addSeconds = station.upgraded
    ? station.selectedMonster.breedingTimeInSecondsEnhanced
    : station.selectedMonster.breedingTimeInSeconds;

  let dateEnd = new Date();
  dateEnd.setSeconds(dateEnd.getSeconds() + addSeconds);

  station.endTimerString = dateEnd.toString();
  station.endTimer = dateEnd;
}

export default defineComponent({
  props: {
    island: {
      type: Object as PropType<Island>,
    },
    type: {
      type: String,
    },
    station: {
      type: Object as PropType<Station>,
    },
    // breedingSations: {
    //   type: Array as PropType<BreedingStation[]>,
    // },
    // nurseryStations: {
    //   type: Array as PropType<NurseryStation[]>,
    // },
  },
  setup(props) {
    let store = useMainStore();

    let onClick = (monster: Monster) => {
      if (
        props.island != undefined &&
        props.station !== undefined &&
        props.station !== null &&
        props.station.index !== undefined &&
        props.station.index !== null &&
        props.station.selectedMonster !== undefined &&
        props.station.selectedMonster !== null &&
        props.station.selectedMonster.name !== monster.name
      ) {
        // store.islands[props.island.index].selectedMonster = monster;
        if (props.type === "breeding") {
          store.islands[props.island.index].breedingStations[
            props.station.index
          ].selectedMonster = monster;
        }

        if (props.type === "nursery") {
          store.islands[props.island.index].nurseryStations[
            props.station.index
          ].selectedMonster = monster;
        }

        // props.station.selectedMonster = monster;
        onStartTimer(props.station);
      }
    };

    let showMonsters = ref(false);

    let onStartTimer = (station: Station) => {
      setEndTimer(station);
    };

    const timeLeft = ref("");
    const isAvailable = computed(() => {
      return timeLeft.value == "";
    });

    onMounted(() => {
      setInterval(() => {
        if (
          props.island != undefined &&
          props.station?.index !== undefined &&
          props.station?.index !== null &&
          props.station?.selectedMonster !== undefined &&
          props.station?.selectedMonster !== null
        ) {
          if (props.type === "breeding") {
            ensureDateIsDateObject(
              store.islands[props.island.index].breedingStations[props.station.index]
            );
          }

          if (props.type === "nursery") {
            ensureDateIsDateObject(
              store.islands[props.island.index].nurseryStations[props.station.index]
            );
          }

          if (props.station.endTimer == null) {
            return;
          }

          const countDownDate = props.station.endTimer;
          const timeUntil = getTimeUntil(countDownDate);

          timeLeft.value = timeUntilToString(timeUntil);
        } else {
          timeLeft.value = "";
        }
      }, 1000);
    });

    let onChangeUpgraded = function () {
      if (
        props.island != undefined &&
        props.station?.index !== undefined &&
        props.station?.index !== null
      ) {
        if (props.type === "breeding") {
          store.islands[props.island.index].breedingStations[
            props.station.index
          ].upgraded = !store.islands[props.island.index].breedingStations[
            props.station.index
          ].upgraded;
        }

        if (props.type === "nursery") {
          store.islands[props.island.index].nurseryStations[
            props.station.index
          ].upgraded = !store.islands[props.island.index].nurseryStations[
            props.station.index
          ].upgraded;
        }
      }
    };

    return {
      onClick,
      showMonsters,
      onStartTimer,
      timeLeft,
      onChangeUpgraded,
      isAvailable,
    };
  },
});
