export enum Element {
    Cold = "COLD",
    Fire = "FIRE",
    Water = "WATER",
    Earth = "EARTH",
    Plant = "PLANT",
    Air = "AIR",
    Light = "LIGHT",
    Psychic = "PSYCHIC",
    Faerie = "Faerie",
    Bone = "BONE",
    Ethereal = "ETHEREAL"
}

export interface MonsterElement {
    name: string,
    iconUrl: string
}

export interface Monster {
    name: string, 
    elements: Element[],
    elementIcons?: string[] | null,
    breedingTimeInSeconds: number,
    breedingTimeInSecondsEnhanced: number,
    breedingTimeString?: string,
    breedingTimeEnhancedString?: string
    monsterIcon?: string
}


export interface MainState {
    islands: Island[]
}

export interface Station {
    upgraded: boolean,
    selectedMonster?: Monster | null,
    index?: number,
    endTimerString?: string,
    endTimer?: Date | null
}

export interface Station { 
    upgraded: boolean,
    selectedMonster?: Monster | null,
    index?: number,
    endTimerString?: string,
    endTimer?: Date | null
}

export interface Island {
    name: string,
    breedingStations: Station[],
    nurseryStations: Station[],
    availableMonsters: Monster[],
    availableElements: Element[],
    availableElementsIcons: string[],

    selectedMonster: Monster,
    index: number,
    endTimerString?: string,
    endTimer?: Date
}

