import { Monster, Element } from "./element";
import { allMonstersWithIcons } from './all-monsters';

export function getMonstersByElements(elements: Element[]): Monster[] {
    const inputElementSet = new Set(elements);

    const outputList: Monster[] = [];

    for (const monster of allMonstersWithIcons) {
        const monsterElementSet = new Set(monster.elements);
        const intersection = new Set(
            [...monsterElementSet].filter(x => !inputElementSet.has(x))
        );

        if (intersection.size > 0) 
            continue;
        
        outputList.push(monster);
    }

    return outputList;
} 

function capitalizeFirstLetter(input: string) {
    return input.charAt(0).toUpperCase() + input.slice(1).toLowerCase();
  }

export function getIconPath(element: Element): string {
    const base_start = "/images/elements/";
    const base_end = "_Element.png";

    return `${base_start}${capitalizeFirstLetter(element)}${base_end}`;
}


export function getMonsterIconPath(monsterName: string): string {
    const base_start = "/images/monsters/";
    const base_end = ".png";

    return `${base_start}${capitalizeFirstLetter(monsterName).replace(" ", "_")}${base_end}`;
}